<template>
  <transition name="slide">
    <div class="user-center">
      <div class="back" @click="back">
        <i class="icon-back"></i>
      </div>
      <div class="switches-wrapper">
        <switches :switches="switches" :currentIndex="currentIndex" @selectItem="switchItem"></switches>
      </div>
      <div class="play-btn" @click="random">
        <i class="icon-play"></i>
        <span class="text">随机播放全部</span>
      </div>
      <div class="list-wrapper" ref="listWrapper">
        <scroll ref="favoriteList" class="list-scroll" v-if="currentIndex === 0" 
          :data="favorite" :pull-up-load="true">
            <div class="list-inner">
              <song-list :songs="favorite" @selectItem="selectSong"></song-list>
            </div>
        </scroll>
        <scroll ref="playList"
          class="list-scroll" v-if="currentIndex === 1" 
          :data="playHistory" :pull-up-load="true">
            <div class="list-inner">
             <song-list :songs="playHistory" @selectItem="selectSong"></song-list>
            </div>
        </scroll>      
      </div>
      <div class="no-result-wrapper" v-show="noResult">
        <no-result :title="noResultDesc"></no-result>
      </div>
    </div>
  </transition>
</template>

<script>
import Switches from 'content/switches/Switches.vue'
import Scroll from 'common/scroll/Scroll.vue'
import SongList from 'content/songlist/SongList.vue'
import NoResult from 'content/noresult/NoResult.vue'
import { mapActions, mapGetters } from 'vuex'
import {Song} from 'network/singerList.js'
import {playlistMixin} from 'common/mixin.js'
  export default {

    data() {
      return {
       switches:[
         {name:'我喜欢的'},
         {name:'最近听的'}
       ],
       currentIndex:0
      }
    },
    components: {
      Switches,
      SongList,
      NoResult,
      Scroll
    },
    computed: {
      ...mapGetters([
        'playHistory',
        'favorite'
      ]),
      noResult(){
        if(this.currentIndex === 0){
          return !this.favorite.length
        }
          return !this.playHistory.length
        
        
      },
      noResultDesc(){
        if(this.currentIndex === 0){
          return '还没有收藏歌曲'
        }
          return '您还没有听过歌曲'
        
        
      }
    },
    methods: {
      switchItem(index){
        this.currentIndex = index
      },
      selectSong(song){
        this.insertSong(new Song(song))
      },
      back(){
        this.$router.back()
      },
      random(){
        let list = this.currentIndex ===0 ? this.favorite : this.playHistory
        if(list.length === 0 ){
          return
        }
        list = list.map((song) => {
          return new Song(song)
        })
        this.randomPlay({list})
      },
      handlePlaylist(playList){
        const bottom = playList.length > 0 ? '60px' : ''
        this.$refs.listWrapper.style.bottom = bottom
        //父级v-if可能让scroll还没出现，所以要先判断一下
        this.$refs.favoriteList && this.$refs.favoriteList.refresh()
        this.$refs.playList && this.$refs.playList.refresh()
      },
      ...mapActions([
        'insertSong',
        'randomPlay'
      ])
    },
    mixins: [playlistMixin]
  }
</script>

<style scoped lang="stylus">
  @import "~assets/stylus/variable"

  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform: translate3d(100%, 0, 0)
    .back
      position absolute
      top: 0
      left: 6px
      z-index: 50
      .icon-back
        display: block
        padding: 10px
        font-size: $font-size-large-x
        color: $color-theme
    .switches-wrapper
      margin: 10px 0 30px 0
    .play-btn
      box-sizing: border-box
      width: 135px
      padding: 7px 0
      margin: 0 auto
      text-align: center
      border: 1px solid  $color-text-l
      color: $color-text-l
      border-radius: 100px
      font-size: 0
      .icon-play
        display: inline-block
        vertical-align: middle
        margin-right: 6px
        font-size: $font-size-medium-x
      .text
        display: inline-block
        vertical-align: middle
        font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>